<template> 
<div>
  <el-transfer
    v-model="value"
    :data="dataList"
    :titles="titlesList"
    :filterable="enabledFilterable"
    :button-texts="buttonsText"
    @change="changeList">
      <template slot="left-footer">
        <!--@slot use this slot leftFooter -->
        <slot name="leftFooter"></slot>
      </template>
      <template slot="right-footer">
        <!--@slot use this slot rightFooter -->
        <slot name="rightFooter"></slot>
      </template>
  </el-transfer>   
</div>
</template>
<script>
  /**
   * Component that transfers items from a list to other list and sends an
   * event with the items of the right list, it has the following additional functions:
   * Sets the text of the list title
   * Sets the text of the transfer button
   * Enables the filterable list
   * @vue UITransfer.vue
   * @version 0.1.0
   * @since 0.1.0
   * @author Alfredo Garcia [gitlab:@garciareal45]
   * @copyright Karanta, CA. 2018
   */
  export default {
    name: 'UiTransfer',

    props: {
  
      /**
       * Property that sets the list of item on the component transfer
       * @prop {Array}
       */
      dataList: {
        type: Array,
        required: true
      },

      /**
       * Property that sets the titles of the two list on the component transfer
       * @prop {Array} [titlesList= [
       *                 'List Title 1',
       *                 'List Title 2'
       *               ]]
       */
      titlesList: {
        type: Array,
        required: false,
        default: function () {
          return [
            'List Title 1',
            'List Title 2'
          ]
        }
      },

      /**
       * Property that enables the filter of the two list on the component transfer
       * @prop {Boolean} [enabledTransfer= false]
       */
      enabledFilterable: {
        type: Boolean,
        required: false,
        default: true
      },

      /**
       * Property that sets the text of the transfer buttons
       * @prop {Array} [buttonText= []]
       */
      buttonsText: {
        type: Array,
        required: false,
        default: function () {
          return []
        }
      }
    },

    data () {
      return {
  
        /**
         * Array that contains the item indices of the right list
         * @prop {Array}
         */
        value: [],

        /**
         * Array that contains the items of the right list
         * @prop {Array}
         */
        list: []
      }
    },

    methods: {
      /**
       * Method that sends the items of the right list when the button
       * transfer is pushed
       * @return {Event}
       * @public
       */
      changeList (index) {
        for (let key of index) {
          this.list.push(this.dataList[key])
        }
        /**
         * event that sends the items of the right list
         * @event on-change-transfer
         * @type {Array}
         */
        this.$emit('on-change-transfer', this.list)
        this.list = []
      }
    }
  }
</script>


